
.mainContainer {
    text-align: center;
    background-image: linear-gradient(225deg, rgb(226,226,226),rgb(197,197,197), rgb(107,107,107));
    background-size: cover;
    background-position: bottom;
    transition: 0.4s ease-out;
    position: relative;
    padding-top: 50px;
    min-height: 100vh;
    font-weight: 500;
    text-shadow: #8f8f8f 2px 2px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.warm{
    background-image: linear-gradient(225deg, rgb(249,218,30), rgb(249,196,40),  rgb(241,92,33));
}
.cold{
    background-image: linear-gradient(225deg, rgb(11,211,198),rgb(51,196,223), rgb(26,118,194));
}

.title{
    color: white;
    font-size: 30px;
    font-weight: 600;
}

.listcontainer{
    display: block;
    margin: 0 20px;
    background-color: rgba(255,255,255,0.2);
    border-radius: 16px;
    padding: 15px 25px;
}
.listPlaceholder{
    color: white;
    font-size: 25px;
}
@media (min-width: 420px) {
 .listContainer{
    width: 80vw;
 }
}