.mainContainer {
    text-align: center;
    background-image: linear-gradient(225deg, rgb(226,226,226),rgb(197,197,197), rgb(107,107,107));
    background-size: cover;
    background-position: bottom;
    transition: 0.4s ease-out;
    position: relative;
    height: 100%;
    min-height: 100vh;
    padding: 25px 25px 25px 25px;
}

.warm{
    background-image: linear-gradient(225deg, rgb(249,218,30), rgb(249,196,40),  rgb(241,92,33));
}
.cold{
    background-image: linear-gradient(225deg, rgb(11,211,198),rgb(51,196,223), rgb(26,118,194));
}
.iconWeather{
    width: 200px;
    height: 200px;
    position: absolute;
    top: 100px;
    right: 0;
    filter: drop-shadow(5px 5px 5px #222);
}

@media (min-width: 420px) {
    .mainContainer {
        padding: 80px 25px 25px 25px;
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .iconWeather{
        top: 250px;
        width: 400px;
        height: 400px;
    }
    .wrapper{
        display: flex;
        justify-content: center;
        gap: 25px;
    }
    .subWrapper{
        display: flex;
        flex-direction: column;
        gap: 25px;
    }
}